  height: 0.33rem;
  line-height: 0.30rem;
  font-size: 0.28rem;
  margin: 0 auto;
  text-align: center;
  color: #fff;
  position: relative;
}
.rule_title_s:before, .rule_title_s:after {
  position: absolute;
  content: "";
  background: #fff;
  height: 1px;
  width: 80%;
  top: 50%;
  position: absolute;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
}
.rule_title_s:before {
  left: -20%;
}
.rule_title_s:after {
  right: -20%;
}
.rule_center {
  margin-top: 0.3rem;
}
.rule_center ul {
  width: 93.33%;
  margin: 0 auto;
}
.rule_center ul li {
  line-height: 0.50rem;
  font-size: 0.26rem;
  color: #fff;
  text-align: justify;
}
/*游戏样式*/
.bigdiv {
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  background: url(./static/img/bj.bd3ba85.png) no-repeat bottom left;
  background-size: 100% auto;
  z-index: 100;
}
.backward {
  width: 100%;
  background: #ccc;
  opacity: 0.5;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}
.backward span {
  display: inline-block;
  width: 4rem;
  height: 4rem;
  color: #000;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  font-size: 3rem;
}
.backward div {
  width: 100%;
  height: 4rem;
  line-height: 4rem;
  color: #000;
  font-weight: bold;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  font-size: 1.4rem;
}
.g {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.ledoucontent {
  position: absolute;
  width: 0;
  height: 1500px;
  top: 0;
  left: 0;
  right: 0;
}
canvas {
  position: fixed;
  top: 0;
  z-index: 5000;
}
@-webkit-keyframes rotate {
from {
    -webkit-transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(360deg);
}
}
@keyframes rotate {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
.gamenumber {
  font-size: 1rem;
  color: #fff;
  font-weight: bold;
  position: absolute;
  bottom: 0.4rem;
  left: 0.2rem;
}
/*游戏弹窗样式*/
.popcontent, .loadingbig {
  background: url(data:image/png;base64,);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 200;
  overflow: hidden;
}
.popdialogue {
  width: 5.66rem;
  height: 3.62rem;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: url(./static/img/Pop.2f8d7ee.png) no-repeat center center;
  background-size: 100% auto;
  margin: auto;
}
.poploding {
  width: 5.66rem;
  height: 3.62rem;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: 100% auto;
  margin: auto;
  margin: auto;
  font-size: 0.4rem;
  text-align: center;
  line-height: 3.62rem;
  color: #fff;
}
.selectBtn {
  width: 3.0rem;
  height: 0.7rem;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -1.5rem;
  display: block;
}
.ledoutext {
  width: 100%;
  position: absolute;
  bottom: 1.24rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  color: #fff;
  font-size: 0.36rem;
}
.ledoutext i {
  color: #fff33b;
  font-size: 0.36rem;
}
.clearBtn {
  width: 0.6rem;
  height: 0.6rem;
  position: absolute;
  right: 0;
  right: 0;
  display: block;
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 100% auto;
}

body {
    margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
